export default {
	props: {
		dateInfo: {
			type: String,
			default: '2020-05'
		}
	},
	data() {
		return {
			date: ''
		}
	},
	computed: {
		dateText () {
			return this.date.replace('-', ' 年 ') + ' 月'
		}
	},
	created () {
		this.date = this.dateInfo
	},
	methods: {
		dateChange (e) {
			let { detail: {value: date } } = e
			this.date = date
			this.$emit('dateChange', date)
		}
	}
}
